<template>
  <v-app>
    <v-main>
      <router-view/>
    </v-main>
  </v-app>
</template>

<style>
/* Предотвращаем автоматический скролл к элементам при фокусе */
html {
  scroll-behavior: auto !important;
}

body {
  overscroll-behavior: none;
  -webkit-overflow-scrolling: touch;
}

/* Убираем bounce эффект */
#app {
  position: relative;
  overflow-x: hidden;
}
</style>

<script>

export default {
  name: 'App',

  data: () => ({
    //
  }),

  mounted() {
    const tg = window.Telegram?.WebApp;
    if (tg) {
      tg.ready();
      tg.expand();
    }

    // Предотвращаем скролл при потере фокуса
    document.addEventListener('click', (e) => {
      const target = e.target;
      const isInput = target.tagName === 'INPUT' || target.tagName === 'TEXTAREA';

      if (!isInput) {
        // Убираем фокус с текущего активного элемента без скролла
        const activeElement = document.activeElement;
        if (activeElement && (activeElement.tagName === 'INPUT' || activeElement.tagName === 'TEXTAREA')) {
          // Сохраняем текущую позицию скролла
          const scrollX = window.scrollX;
          const scrollY = window.scrollY;

          activeElement.blur();

          // Восстанавливаем позицию скролла после blur
          requestAnimationFrame(() => {
            window.scrollTo(scrollX, scrollY);
          });
        }
      }
    });
  },
}
</script>
