import {createRouter, createWebHashHistory} from 'vue-router'

const routes = [
    {
        path: '/',
        name: 'pages',
        component: () => import(/* webpackChunkName: "pages" */ '../views/PagesListView.vue')
    },
    {
        path: '/page/:id',
        name: 'page-detail',
        component: () => import(/* webpackChunkName: "page-detail" */ '../views/PageDetailView.vue')
    },
    {
        path: '/subscription',
        name: 'subscription',
        component: () => import(/* webpackChunkName: "subscription" */ '../views/Subscription.vue')
    },
    {
        path: '/subscription/plans',
        name: 'subscription-plans',
        component: () => import(/* webpackChunkName: "subscription-plans" */ '../views/SubscriptionPlans.vue')
    }
]

const router = createRouter({
    history: createWebHashHistory(),
    routes
})

export default router
